







































import { Vue, Component, Prop } from 'vue-property-decorator'
@Component
export default class LogList extends Vue {
  @Prop(Array)
  keys!: any[]

  @Prop(Array)
  list!: Connex.Thor.Log<'decoded-event'>[]

  @Prop({ default: false })
  metadata!: boolean

  get blockKeys() {
    if (this.metadata) {
      return ['blockNumber', 'blockID', 'txID', 'blockTimestamp']
    } else {
      return []
    }
  }
}
