


































import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
@Component
export default class Panel extends Vue {
  @Prop({ default: 0 })
  value!: string

  @Prop({ default: Array })
  tabs!: string[]

  @Prop({ default: '' })
  title!: string

  @Watch('value')
  onchange(val: string) {
    this.activeTab = val
  }

  private activeTab: string = this.value

  private switchTab(tab: string) {
    this.activeTab = tab
    this.$emit('input', tab)
  }
}
