
























import { Vue, Prop, Component, Watch } from 'vue-property-decorator'
import DB, { Entities } from '../database'
@Component
export default class EditContract extends Vue {
  @Prop({ default: null })
  private item!: Entities.Contract | null

  private form = {
    id: 0,
    name: '',
    address: '',
    abi: ''
  }

  created() {
    this.initForm()
  }

  get context() {
    let result = {
      title: 'Add Contract',
      btn: 'Add'
    }

    if (this.isEdit) {
      result.title = 'Edit Contract'
      result.btn = 'Edit'
    }

    return result
  }

  get isEdit() {
    return this.item && this.item.address
  }
  close() {
    this.$emit('cancel')
  }

  initForm() {
    const val = this.item
    if (val && val.address) {
      this.form.name = val.name || ''
      this.form.address = val.address || ''
      this.form.abi = val.abi || ''
      this.form.id = val.id || 0
    } else {
      this.form = {
        name: '',
        address: '',
        abi: '',
        id: 0
      }
    }
  }

  async submit() {
    const obj: Entities.Contract = {
      name: this.form.name,
      address: this.form.address,
      abi: this.form.abi,
      createdTime: Date.now()
    }
    try {
      if (!this.isEdit) {
        await DB.contracts.add(obj)
      } else {
        await DB.contracts.where('id').equals(this.form.id).modify(obj)
      }
    } catch (error) {
      console.error(error)
    } finally {
      this.$emit('finished')
    }
  }
}
